@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
:root {
  --primary: 88, 106, 106;
  --secondary: 211, 185, 159;
  --tertiary: 66, 191, 221;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* button {
  border: 0;
}

.wrapper {
  max-width: 100%;
  height: 100vh;
  background-color: #000;
  overflow-x: hidden;
}

.main {
  position: relative;
  height: 100%;
}

.main-image {
  width: 100%;
  position: absolute;
  cursor: crosshair;
}

header {
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 0.5rem 0;
  opacity: 0.95;
  width: 20%;
  left: 50%;
  margin-left: -10%;
}

.images-container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.images-container-box {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  width: 30%;
  margin: 0 0.5rem;
  border: 0.1rem #000 solid;
  border-radius: 0.5rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  background-color: #aaa;
}

.images-container img {
  width: 100%;
  height: 100%;
  order: 1;
}

.images-container-box p {
  font-family: 'Roboto Mono', monospace;
  font-size: 1.2vw;
  text-align: center;
}

@media only screen and (max-width: 1200px) {
  header {
    width: 30%;
    margin-left: -15%;
  }
}

@media only screen and (max-width: 500px) {
  header {
    width: 40%;
    margin-left: -20%;
  }
}

.dropdown {
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
  width: 9rem;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  overflow: hidden;
  border-radius: 0.8rem;
  -webkit-transform: translateX(-40%);
          transform: translateX(-40%);
}

.dropdown button {
  height: 100%;
  font-family: 'Roboto Mono', monospace;
  font-size: 1rem;
  padding: 0.5rem 2rem;
  background-color: #ddd;
  transition: background-color 0.4s;
  cursor: pointer;
}

.dropdown button:hover {
  background-color: #ccc;
}

.loader {
  background-color: rgb(var(--primary));
  color: rgb(var(--secondary));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 2;
  font-size: 1.5rem;
  font-family: 'Roboto Mono', monospace;
}

.timer {
  position: fixed;
  z-index: 1;
  top: 2%;
  right: 2%;
  font-size: 4vw;
  font-family: 'Staatliches', cursive;
  color: #fff;
  text-shadow: 2px 2px 4px #000000;
}

.start {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1;
}

.start-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(var(--primary), 0.9);
  width: 25%;
  border-radius: 0.5rem;
  padding: 1rem 0;
  font-family: 'Roboto Mono', monospace;
  text-align: center;
  color: rgb(var(--secondary));
}

.start-box button {
  padding: 0.2rem 2rem;
  background-color: rgb(var(--tertiary), 0.6);
  border-radius: 0.5rem;
  font-family: 'Roboto Mono', monospace;
  font-size: 1rem;
  transition: all 0.2s ease-in-out;
  margin-bottom: 0.5rem;
  cursor: pointer;
}

.start-box-images {
  display: flex;
  width: 90%;
  justify-content: center;
  margin: 1rem 0;
}

.start-box-images-box {
  display: flex;
  flex-direction: column;
  width: 33%;
  text-align: center;
  margin: 0 0.5rem;
}

.start-box-images img {
  transition: all 0.2s ease-in-out;
  margin: 0.3rem;
}

.start-box button:hover,
.start-box-images-box img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.start-box-footer {
  width: 100%;
  margin-left: 3rem;
}

.start-box-footer a {
  text-decoration: none;
  color: rgb(var(--tertiary));
}

.start-box-footer p {
  margin: 0.3rem 0;
  font-size: 0.8rem;
  text-align: left;
}

@media only screen and (max-width: 1200px) {
  .start-box {
    width: 50%;
  }
}

@media only screen and (max-width: 600px) {
  .start-box {
    width: 80%;
  }
}

.message {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 30%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  left: 50%;
  background-color: rgb(var(--primary), 0.9);
  padding: 0.3rem 0.6rem;
  border-radius: 0.5rem;
  z-index: 1;
  font-family: 'Roboto Mono', monospace;
}

.message-failure {
  color: rgb(128, 0, 32);
}

.message-success {
  color: rgb(0, 153, 25);
}

.scoreboard {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1;
}

.scoreboard-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(var(--primary), 0.9);
  width: 25%;
  border-radius: 0.5rem;
  padding: 1rem;
  font-family: 'Roboto Mono', monospace;
  text-align: center;
  color: rgb(var(--secondary));
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

#scoreboard-box-header {
  font-size: 1.1rem;
}

#scoreboard-box-header span {
  color: rgb(var(--tertiary));
}

.scoreboard-entries {
  width: 100%;
  border-radius: 0.5rem;
  overflow: hidden;
}

.scoreboard-entry {
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: rgb(var(--primary));
  padding: 0.5rem 1rem;
}

.scoreboard-last-entry {
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.scoreboard-input {
  width: 60%;
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  flex-direction: column;
  align-items: center;
}

.scoreboard-input input[type='text'] {
  width: 100%;
  padding: 0.5rem;
  border: 0;
  border-radius: 0.5rem;
}

.scoreboard-input button {
  border: 0;
  width: 100%;
  padding: 0.3rem 2rem;
  background-color: rgb(var(--tertiary), 0.6);
  border-radius: 0.5rem;
  font-family: 'Roboto Mono', monospace;
  font-size: 1rem;
  cursor: pointer;
}

.scoreboard-thanks {
  margin: 1rem 0;
  font-size: 1.3rem;
  font-weight: 600;
}

@media only screen and (max-width: 1200px) {
  .scoreboard-box {
    width: 50%;
  }
}

@media only screen and (max-width: 769px) {
  .scoreboard-box {
    width: 80%;
  }
}

@media only screen and (max-width: 376px) {
  .scoreboard-box {
    width: 95%;
  }
}

